import config from "./config.js"
export default function initApp() {
	/**
	 * 
	 */
	let list = ["switchTab"];
	list.forEach(item => { //,uni.navigateTo,uni.redirectTo,uni.reLaunch,uni.switchTab4
		uni.addInterceptor(item, {
			invoke(e) { // 
				//token
			    const token = uni.getStorageSync('token'),
				//url"?""?"
				url = e.url.split('?')[0];
				let notNeed = config.whiteList.includes(url)
				// whiteList
				if (notNeed) {
					return e
				} else {
					//
					if (!token) {
						uni.navigateTo({
							url: '/pages/login/login'
						})
						setTimeout(()=>{
							uni.showToast({
								title: '',
								icon: 'none'
							})
						},1000)
						return false
					}else{
						return e
					}
				}
				
			},
			fail(err) { //  
				console.log(err);
				if (Debug) {
					console.log(err);
					uni.showModal({
						content: JSON.stringify(err),
						showCancel: false
					});
				}
			}
		})
	})
}

